import React from 'react'
import { withPrefix } from 'gatsby'

// material-ui
import AppBar from '@material-ui/core/AppBar/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import IconButton from '@material-ui/core/IconButton'
import ClearIcon from '@material-ui/icons/Clear'
import Typography from '@material-ui/core/Typography'

// custom components
import SEO from '../SEO/SEO'
import ElevationScroll from '../ElevationScroll/ElevationScroll'

// hook queries site meta data
import useSiteMetaData from '../../data/useSiteMetaData'

/**
 * Header component for article page
 * @param {Object} props - component properties
 * @param {String} props.title - the title of the article
 * @param {String} props.description - the description of the article (excerpt)
 * @param {Object} props.location - representing where the app is from a router perspective
 * @param {Function} props.navigate - provides a means to programmatically navigate
 * @param {*} props.children - children components
 * @returns {*}
 * @constructor
 */
function ArticleHeader({ title, description, location, navigate, children }) {
  const siteMeta = useSiteMetaData()
  const { state = {} } = location
  const to = state && state.referrer ? state.referrer : withPrefix('/')

  return (
    <React.Fragment>
      <SEO title={title} description={description} siteMeta={siteMeta} />
      <ElevationScroll>
        <AppBar color="primary" position="fixed">
          <Toolbar>
            <IconButton onClick={() => navigate(to)} edge="start" color="inherit"  aria-label="menu">
              <ClearIcon />
            </IconButton>
            <Typography variant="subtitle1" color="inherit">
              {title}
            </Typography>
          </Toolbar>
        </AppBar>
      </ElevationScroll>
      {children}
    </React.Fragment>
  )
}

export default ArticleHeader
