import React from 'react'
import { graphql } from 'gatsby'

// Styles for rendered markdown
import '../styles/index.css'

// material-ui
import Toolbar from '@material-ui/core/Toolbar'
import Box from '@material-ui/core/Box'
import Typography from '@material-ui/core/Typography'
import Card from '@material-ui/core/Card'
import Container from '@material-ui/core/Container'
import CardMedia from '@material-ui/core/CardMedia'
import Divider from '@material-ui/core/Divider'
import Fab from '@material-ui/core/Fab'
import KeyboardArrowUpIcon from '@material-ui/icons/KeyboardArrowUp'
import { withStyles } from '@material-ui/core/styles'

// custom components
import ArticleHeader from '../components/Headers/ArticleHeader'
import ScrollTop from '../components/ScrollTop/ScrollTop'
import Footer from "../components/Footer/Footer"

const styles = theme => ({
  cardMedia: {
    height: '210px'
  },
  date: {
    paddingTop: theme.spacing(2),
    fontSize: 14
  },
  author: {
    fontSize: 14
  }
})

/**
 * Template component to create an project page
 * @param {Object} props - component properties
 * @param {Object} props.classes - override the injected class names
 * @param {Object} props.data - containing the data for an article
 * @param {Object} props.location - representing where the app is from a router perspective
 * @param {Function} props.navigate - provides a means to programmatically navigate
 * @returns {*}
 * @constructor
 */
function ProjectPageTemplate({ classes, data, location, navigate }) {
  const { html, excerpt: description, timeToRead, frontmatter } = data.markdownRemark
  const { title, subtitle, date, publishedBy, image: { publicURL } } = frontmatter

  return (
    <ArticleHeader
      title={title}
      subtitle={subtitle}
      date={date}
      PublishedBy={publishedBy}
      timeToRead={timeToRead}
      description={description}
      location={location}
      navigate={navigate}
    >
      {/* target for scrollTop component - hidden behind header since header is fixed */}
      <Toolbar id="back-to-top-anchor" />
      <Box clone pt={2} pb={2}>
        <Container maxWidth="md">
          <Card elevation={1}>
            <CardMedia className={classes.cardMedia} image={publicURL} />
            <Box pt={2} pr={2} pb={2} pl={2}>
              <div dangerouslySetInnerHTML={{ __html: html }} />
              <Box pt={1} pb={1}>
                <Divider variant="fullWidth" />
              </Box>
              <Typography variant="subtitle2" color="textSecondary">
                Published: {date}
              </Typography>
              <Typography variant="subtitle2" color="textSecondary">
                PublishedBy: {publishedBy}
              </Typography>
            </Box>
          </Card>
        </Container>
      </Box>
      <ScrollTop>
        <Fab color="secondary" aria-label="scroll back to top">
          <KeyboardArrowUpIcon />
        </Fab>
      </ScrollTop>
      <Footer/>
    </ArticleHeader>
  )
}

/**
 * Query news data by slug to inject as props
 */
export const query = graphql`
  query projectPageQuery($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      frontmatter {
        title
        subtitle
        date(formatString: "YYYY-MM-DD")
        publishedBy
        image {
          publicURL
        }
      }
      excerpt
      timeToRead
      html
    }
  }
`

export default withStyles(styles)(ProjectPageTemplate)
